import styled from "styled-components";

export const Main = styled.div`
    height: 100vh ;
    display: flex ;
    text-align: center ;
    justify-content: center ;
    align-items: center ;
    flex-direction: column ;
    background: linear-gradient(#121212, #212b46) ;
    .title{
        color: #FFF ;
        font-size: 70px ;
        animation: flipTitle 1s;
        
        }
    .main-input{
       background-color: rgba(255, 255, 255, 0.2) ;
       padding: 15px ;
       margin: 34px 0;
       display: flex ;
       border-radius: 8px ;
       box-shadow: 1px 3px 8px rgba(0,0,0,0.5) ;
        input{
            background-color: transparent ;
            border: 0 ;
            font-size: 20px ;
            color: #FFF ;
            outline: none ;
            margin-right: 8px ;
        }
        input::placeholder{
            color: #F1f1f1 ;

        }
        .search{
            background-color: transparent ;
            border: 0 ;
            display: flex ;
            align-items: center ;
            justify-content: center ;
            cursor: pointer ;
            transition: transform 0.5s ;
        }
        .search:hover{
            transform: scale(1.2) ;
        }
        
    }
    .info{
            display: flex ;
            justify-content: center ;
            align-items: center ;
            flex-direction: column ;
            background-color: #FFF ;
            border-radius: 8px ;
            width: 27rem ;
            box-shadow: 1px 3px 8px rgba(0,0,0,0.5) ;
            animation: flipTitle 1s;
            h1{
                margin: 16px 0 ;
                font-size: 39px ;
            }
            span{
                margin-bottom: 16px;
                font-weight: bold ;
            }
            
        }
    @keyframes flipTitle {
            from{
                transform: scale(1.1) ;
            }
            to{
                transform: scale(1) ;
            }
        }
    @media (max-width: 620px){
        .title{ 
            font-size: 55px ;
        }       
        .info h1{
            font-size: 30px ;
        }
        

            }

`   
